import Vue from "vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import App from "./App.vue";
import store from "./store/store";
import router from "./router/router";
import axios from "axios";
import Vueaxios from "vue-axios";
import Qs from "qs";
import animate from "animate.css";
import * as functions from "./app/modules/functions";

// axios
const axiosInstance = axios.create({
  // configtransformRquestQs.stringify
  transformRequest: [
    function(data) {
      data = Qs.stringify(data);
      return data;
    }
  ],

  // Content-Type
  headers: { "Content-Type": "application/x-www-form-urlencoded" },

  // cookies
  withCredentials: true,
  baseURL:
    process.env.NODE_ENV === "production"
      ? "http://sharingideas.cn/"
      : "/api" // url
});

Vue.use(animate);
Vue.use(ElementUI);
Vue.use(Vueaxios, axiosInstance);
Vue.config.productionTip = false;
Vue.prototype.$fn = functions;

// 
store.commit("changeMode", window.innerHeight > window.innerWidth);

// 
window.onresize = () => {
  store.commit("changeMode", window.innerHeight > window.innerWidth);
};
/**
* 
* axsio
* Ajax
*/
window.onbeforeunload = ()=>{
  let xhr;
  if (window.XMLHttpRequest) {
    // code for IE7+, Firefox, Chrome, Opera, Safari
    xhr = new XMLHttpRequest();
  } else {
  // code for IE6, IE5
  xhr = new ActiveXObject("Microsoft.XMLHTTP");
  }
  //
  let apiPath = process.env.NODE_ENV === "production"
  ? "dataStatistics/access"
  : "api/dataStatistics/access" // url
  xhr.open("POST",apiPath, false);
  xhr.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
  // lastURI: "/",          //uri
  // nextURI: "/resources",      //uri
  let lastURI = this.$route.path;
  let nextURI = null;
  // let parm = "lastURI=" + lastURI + "&nextURI=" + nextURI;
  let postData = {"lastURI":lastURI,"nextURI":nextURI};
  xhr.send(postData);
};
// 
// window.onresize = throttle(() => {
//   store.commit('changeMode', window.innerHeight > window.innerWidth);
// }, 1000);

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
